.article {
  width: 100%;
  max-width: 360px;
  margin: 0 auto;

  &__first {
    margin-top: 47px;
  }

  &__second {
    margin-top: 54px;
  }

  &__third {
    margin-top: 47px;
  }

  &__fourth {
    margin-top: 56px;
  }

  &__fifth {
    margin-top: 47px;
  }

  &__sixth {
    margin-top: 56px;
  }

  &__link {
    display: block;

    color: $font-black;
  }

  &__pic {
    position: relative;

    width: 360px;
    max-width: 100%;
    overflow: hidden;

    &::after {
      content: "";
      position: absolute;
      bottom: -1px;
      left: 50%;
      z-index: 100;

      width: 0;
      height: 0;
      margin-left: -13px;

      border-top: solid 13px #ffffff;
      border-right: solid 13px transparent;
      border-left: solid 13px transparent;
      transform: rotate(180deg);
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: -1;

      width: 100%;
      height: 100%;

      background-image: linear-gradient(to top, #212121, #d4d5d0);
      opacity: 0.4;
    }
  }

  &__img {
    width: 100%;
    height: auto;
  }

  &__date {
    position: absolute;
    bottom: 8px;
    left: 16px;
    z-index: 99;

    font-weight: 300;
    font-size: 0.875rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-white;
  }

  &__comments {
    position: absolute;
    right: 15px;
    bottom: 9px;
    z-index: 99;
  }

  &__subheader {
    margin-top: 3px;

    letter-spacing: -0.5px;
  }

  &__title {
    margin-top: 26px;
    margin-bottom: 0;

    font-weight: 600;
    font-size: 1.375rem;
    font-family: "Raleway", "Arial", sans-serif;
    color: $font-black;
  }

  &__tags {
    display: inline-block;
  }

  &__tag {
    font-weight: 600;
    font-size: 0.75rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-black;
  }

  &__author {
    font-weight: 600;
    font-size: 0.75rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-black;
  }

  &__author-link {
    font-weight: 600;
    font-size: 0.75rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-green;
  }

  &__text {
    margin-top: 5px;
    margin-bottom: 0;

    font-weight: 400;
    font-size: 0.875rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-grey;
    letter-spacing: -0.2px;
  }

  &__footer {
    margin-top: 13px;
  }

  &__more {
    font-weight: 600;
    font-size: 0.875rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-black;
  }
}

.article-comments {
  &__count {
    margin-left: 4px;

    font-weight: 300;
    font-size: 0.875rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-white;
  }
}

.article-more {
  &__arrow {
    margin-left: 2px;
  }
}

.tag {
  &__comma {
    margin-left: -3px;

    font-weight: 600;
    font-size: 0.75rem;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-black;
  }
}

.article-first {
  &__pic {
    max-height: 240px;
  }
}

.article-second {
  &__pic {
    max-height: 500px;
  }
}

.article-third {
  &__pic {
    max-height: 400px;
  }
}

.article-fourth {
  &__pic {
    max-height: 240px;
  }
}

.article-fifth {
  &__pic {
    max-height: 280px;
  }
}

.article-sixth {
  &__pic {
    max-height: 500px;
  }
}
