.top {
  &__bar {
    margin-top: 35px;
  }
}

.top-bar {
  display: flex;
  align-items: center;
}

.menu {
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  width: 39px;
  height: 35px;

  cursor: pointer;

  &__line {
    width: 38px;
    height: 5px;

    background-color: $font-black;

    &--middle {
      width: 27px;
    }
  }
}
