.subscribe {
  padding-top: 57px;
  padding-bottom: 60px;

  background-color: $bg-light-grey;

  &__header {
    margin-bottom: 0;

    font-weight: 600;
    font-size: 1.375rem;
    font-family: "Montserrat", "Arial", sans-serif;
    letter-spacing: -0.3px;
  }

  &__email-input {
    width: 100%;
    max-width: 362px;
    margin-top: 23px;
    padding: 20px 13px;

    font-weight: 400;
    font-size: 0.875rem;
    line-height: 1;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-light-grey;

    border: 1px solid $subscribe-input-border;

    &::placeholder {
      font-weight: 400;
      font-family: "Montserrat", "Arial", sans-serif;
      color: $font-light-grey;
    }

    @media (max-width: 767px) {
      text-align: center;
    }
  }

  &__btn {
    margin-top: -2px;
    margin-left: 25px;

    @media (max-width: 767px) {
      width: 100%;
      max-width: 362px;
      margin-left: 0;
      padding: 22px 0;
    }
  }
}
